body {
  background-color: #181818;
  color: white;
  user-select: none;
  /* cursor: url(https://www.dropbox.com/s/5s7mmmwn1mtuvnl/cursor-v3.png?dl=1),
    move; */
  cursor: none;
  /* font-family: 'Libre Baskerville', serif; */
  font-family: orpheuspro, serif;
}

.flex-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.flex-item-1 {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  align-items: flex-end;
}

.flex-item-2 {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: flex-start;
}

.images {
  max-width: 100%;
  min-width: 200px;
  height: auto;
  margin: 20px;
  /* margin-top: 30vh; */
  margin-top: 15vw;
}

.image-1 {
  align-self: flex-start;
}

.image-2 {
  align-self: center;
}

.image-3 {
  align-self: flex-end;
}

.first-right-image {
  margin-top: 30vh;
}

.first-left-image {
  margin-top: 5vh;
}

.label {
  position: relative;
  padding: 20px;
  font-size: 2.2vw;
  /* font-size: calc(20px + 1.7vw); */
  text-align: justify;
  color: white;
  margin-top: 30vh;
  transition: top 0.5s linear;
}

.title {
  text-align: right;
  font-size: 12vw;
  position: absolute;
  /* top: 55vh; */
  right: 1vw;
  color: white;
  transition: top 0.1s linear;
}

.image-box {
  position: relative;
  display: inline-block;
}

.image-box img {
  transition: all linear 0.5s;
}

img.hover {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.image-box img:hover {
  opacity: 1;
}

@media only screen and (max-width: 600px) {
  .images {
    margin-top: 5vh;
  }

  .image-1,
  .image-3 {
    align-self: center;
  }

  .label {
    margin-top: 0;
  }

  .first-right-image {
    margin-top: 0;
  }
}
